import React, { useState, userRef, useEffect } from 'react'
import {Link} from 'react-router-dom'

function navbar() {
    return (
        <div>
            <nav className="navbar navbar-dark bg-dark navbar-expand-sm">
                <div className="container">
                    <Link to="/" className="navbar-brand">Utility App</Link>
                <ul className="nav">
                    <li className="nav-item"><Link className="nav-link text-white" to="/">Home</Link></li>
                    <li className="nav-item"><Link className="nav-link text-white" to="/profile">Profile</Link></li>
                    <li className="nav-item"><Link className="nav-link text-white" to="/weather">Weather App</Link></li>
                    <li className="nav-item"><Link className="nav-link text-white" to="/Calculator">Calculator</Link></li>
                    <li className="nav-item"><Link className="nav-link text-white" to="/information">Information</Link></li>
                </ul>
                </div>
            </nav> 
        </div>
    )
}
export default navbar